import { View } from '@lightningjs/solid';
import { Column } from '@lightningjs/solid-primitives';
import styles from '../../assets/styles/homeStyles';
import { For, createSignal, onMount } from 'solid-js';
import { TileRow } from '../components';

const HomePage = () => {
  const [listChannels, setListChannels] = createSignal<any>([]);
  function command(jsonString) {
    try {
        let jsonData = window['CrossPlatformBridge'].command(jsonString)
        let object = JSON.parse(jsonData)
        return object
    } catch (e: any) {
        return null
    }
  }

  function ACPB1() {
    let data = {
        "param": {
            "service_no": "A_CPB1",
            "version": "1"
        },
        "payload": {
            "type": "all"
        }
    }

    let channelList = command(JSON.stringify(data))
    if (channelList && channelList.data) {
      return channelList.data
    }
  }

  onMount(async () => {
    const data = await ACPB1();
    if(data && data.data.length > 0) {
      setListChannels(data.data);
    }
  });

  return (
    <View style={styles.HomeWrapper}>
      <Column plinko>
        <For each={listChannels()}>
          {(items, i) =>
            <View key={`view_${i}`}>{items.id}</View>
          }
        </For>
      </Column>
    </View>
  );
};

export default HomePage;
